

@font-face {
    font-family: 'Kelly Slab';
    src: url('../fonts/kellyslab-regular/kellyslab-regular.eot');
    src: url('../fonts/kellyslab-regular/kellyslab-regular.eot?#iefix') format('embedded-opentype'),
         url('../fonts/kellyslab-regular/kellyslab-regular.woff2') format('woff2'),
         url('../fonts/kellyslab-regular/kellyslab-regular.woff') format('woff'),
         url('../fonts/kellyslab-regular/kellyslab-regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;

}

